<template>
    <div>
        <div class="search_ipt">
            <el-input prefix-icon="el-icon-search" style="width: 400px;margin-right: 10px;" v-model="keywords" placeholder="通过用户名搜索用户....."
                      @keydown.enter.native="searchUser"></el-input>
            <el-button type="primary" icon="el-icon-search" @click="searchUser" >搜索</el-button>
        </div>
        <div class="user_container">
            <el-card class="box-card" v-for="(user,index) in users" :key="index" style="width:350px;">
                <div slot="header" class="clearfix">
                    <span>{{user.username}}</span>
                    <el-button style="float: right; padding: 3px 0;color: red"  icon="el-icon-delete" @click="deleteUser(user.id)"></el-button>
                </div>
                <div>
                    <div class="img_container">
                        <img :src="user.userFace"  :title="user.name" class="usrFace_img"/>
                    </div>
                    <div class="userinfo_container">
                        <div>用户账号：{{user.username}}</div>
                        <div>手机号：{{user.telephone}}</div>
                        <div>用户地址：{{user.address}}</div>
                        <div>用户名：{{user.name}}</div>
                        <div>用户状态：
                            <el-switch
                                v-model="user.enabled"
                                active-text="启用"
                                active-color="#13ce66"
                                inactive-color="#ff4949"
                                inactive-text="禁用"
                                @change="enabledChange(user)">
                            </el-switch>
                        </div>
                        <div>用户角色：
                            <el-tag v-for="(role,indexj) in user.roles" type="success" :key="indexj" style="margin-right: 5px">
                                {{role.nameZh}}
                            </el-tag>  <el-popover
                                    placement="bottom"
                                    title="角色"
                                    width="200"
                                    @show="showRoles(user.roles)"
                                    @hide="updateUserRole(user)"
                                    trigger="click"
                                    ><el-select v-model="showCheckeds" multiple placeholder="请选择">
                                <el-option
                                        v-for="(role,indexk) in allRoles"
                                        :key="indexk"
                                        :label="role.nameZh"
                                        :value="role.id">
                                </el-option>
                            </el-select>
                                <el-button slot="reference" icon="el-icon-more" type="text"></el-button>
                            </el-popover>
                        </div>
                    </div>
                </div>
            </el-card>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                keywords:'',
                users:[],
                allRoles:[],
                showCheckeds:[]
            }
        },
        mounted() {
            this.initUsers();
        },
        methods:{
            searchUser(){
                this.initUsers();
            },
            updateUserRole(user){
                let roles = [];
                Object.assign(roles, user.roles);
                let flag = false;
                if (roles.length != this.showCheckeds.length) {
                    flag = true;
                } else {
                    for (let i = 0; i < roles.length; i++) {
                        let role = roles[i];
                        for (let j = 0; j < this.showCheckeds.length; j++) {
                            let sr = this.showCheckeds[j];
                            if (role.id == sr) {
                                roles.splice(i, 1);
                                i--;
                                break;
                            }
                        }
                    }
                    if (roles.length != 0) {
                        flag = true;
                    }
                }
                if(flag){
                    let url = "/account/roleManage//updateUser?uid="+user.id
                    this.showCheckeds.forEach(item=>{
                        url += "&rids="+item
                    })
                    this.putRequest(url).then(rsp=>{
                        if(rsp){
                            this.initUsers();
                        }
                    })
                }

            },
            showRoles(roles){
                this.initRoles();
                this.showCheckeds = []
                roles.forEach(r=>{
                    this.showCheckeds.push(r.id)
                })
            },
            initRoles(){
              this.getRequest("/account/roleManage/allRoles").then(rsp=>{
                  if(rsp){
                      this.allRoles = rsp;
                  }
              })
            },
            initUsers(){
                this.getRequest("/account/roleManage/?keywords="+this.keywords).then(rsp=>{
                    if(rsp){
                        this.users = rsp;
                    }
                })
            },
            enabledChange(user){
                delete user.roles;
                this.putRequest("/account/roleManage/",user).then(rsp=>{
                    if(rsp){
                        this.initUsers();
                    }
                })
            },
            deleteUser(id){
                this.deleteRequest("/account/roleManage/"+id).then(rsp=>{
                    if(rsp){
                        this.initUsers();
                    }
                })
            }
        },
        name: "ActRolManage"
    }
</script>

<style>
    .user_container{
        display: flex;
        justify-content: space-around;
        margin-top: 20px;
        flex-wrap: wrap;
    }
    .usrFace_img{
        width: 72px;
        height: 72px;
        border-radius: 72px;
    }
    .search_ipt{
        display: flex;
        justify-content: center;
    }
    .img_container{
        display: flex;
        justify-content: center;
        width: 100%;
    }
    .userinfo_container{
        margin-top: 20px;
    }
    .userinfo_container div{
        font-size: 12px;
        color: #65a6ff;
    }
</style>