<template>
    <div>
        <div class="role_ipt">
            <el-input placeholder="请输入角色英文名" v-model="role.name">
                <template slot="prepend">ROLE_</template>
            </el-input>
            <el-input placeholder="请输入角色中文名" v-model="role.nameZh" @keydown.enter.native="addRole"></el-input>
            <el-button type="primary" size="small" icon="el-icon-plus" @click="addRole">添加角色</el-button>
        </div>
        <div class="role_coll">
            <el-collapse accordion @change="change" v-model="activeName"
                         class="role_coll"
                         v-loading="add_loading"
                         element-loading-text="正在添加..."
                         element-loading-spinner="el-icon-loading"
                         element-loading-background="rgba(0, 0, 0, 0.8)">
                <el-collapse-item :title="r.nameZh"  :name="r.id" v-for="(r,index) in roles"  :key="index">
                    <el-card class="box-card">
                        <div slot="header" class="clearfix">
                            <span>{{r.nameZh}}</span>
                            <el-button style="float: right; padding: 3px 0;color: red" type="text" icon="el-icon-delete" @click="deleteRole(r.id)"></el-button>
                        </div>
                        <div class="text item">
                            <el-tree ref="tree" :key="index" :data="menus" :props="defaultProps" show-checkbox node-key="id"
                                     :default-checked-keys="checked_menu" ></el-tree>
                            <div style="display: flex;justify-content: flex-end">
                                <el-button @click="cancelUpdate">取消修改</el-button>
                                <el-button @click="doUpdate(r.id,index)" type="primary">确认修改</el-button>
                            </div>
                        </div>
                    </el-card>
                </el-collapse-item>

            </el-collapse>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                role: {
                    name: '',
                    nameZh: ''
                },
                activeName: 1,
                roles: [],
                menus: [],
                defaultProps: {
                    children: 'children',
                    label: 'name'
                },
                loading: false,
                add_loading:false,
                checked_menu:[],
                activeName:-1
            }
        },
        mounted() {
            this.initRoles()
        },
        methods:{
            addRole(){
                if(this.role.name && this.role.nameZh){
                    this.add_loading = true;
                    this.postRequest("/account/permissManage/add",this.role).then(rsp=>{
                        this.add_loading = false;
                        this.role.name = "";
                        this.role.nameZh = "";
                        this.initRoles();
                    })
                }else{
                    this.$message.error("请输入要添加的角色名");
                }

            },
            cancelUpdate(){
                this.activeName = -1;
            },
            doUpdate(id,index){
                let tree = this.$refs.tree[index];
                let mIds = tree.getCheckedKeys(true);
                let url = "/account/permissManage/?rId="+id
                if(mIds.length > 0){
                    mIds.forEach(mId=>{
                        url += "&ckMenus="+mId;
                    })
                }else{
                    url += "&ckMenus="+[];
                }
                this.putRequest(url).then(rsp=>{
                    this.activeName = -1;
                })
            },
            deleteRole(rId){
                this.$confirm('此操作将永久删除该角色, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/account/permissManage/role/"+rId).then(rsp=>{
                        this.initRoles();
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消注销'
                    });
                });
            },
            change(rId){
                if(rId){
                    this.checked_menu = []
                    this.initMenus();
                    this.checkMenus(rId)
                }
            },
            checkMenus(rId){
                this.getRequest("/account/permissManage/"+rId).then(rsp=>{
                    this.checked_menu = rsp;
                })
            },
            initMenus(){
                this.getRequest("/account/permissManage/menus").then(rsp=>{
                    this.menus = rsp;
                })
            },
            initRoles(){
                this.loading = true;
                this.getRequest("/account/permissManage/").then(rsp=>{
                    this.loading = false;
                    if(rsp){
                        this.roles = rsp
                    }
                })
            }
        },
        name: "ActPermissManage"
    }
</script>

<style>
.role_ipt{
    display: flex;
    justify-content: flex-start;
}
.role_ipt .el-input{
    width:300px;
    margin-right: 10px;
}
.role_coll{
    margin-top: 15px;
    width:700px
}
</style>